import {Pipe, PipeTransform} from '@angular/core'

@Pipe({name: 'sentence'})
export class SentencePipe implements PipeTransform {

    transform(value:any) {
        if (value) {
            return value.charAt(0).toUpperCase() + value.slice(1);
        }
        return value;
    }

}


