import { Component } from '@angular/core'
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular'
import { Network } from '@ionic-native/network';


//pages
  import { RegisterPage } from '../../pages/register/register'
  import { ForgotPage } from '../../pages/forgot/forgot'
  import { LoginNativePage } from '../../pages/login-native/login-native'

//services
  import { EventProvider } from '../../providers/event/event';
  import { DatabaseProvider } from '../../providers/database/database';


//Models
  import { AppInstance } from '../../models/app_instance'

//Const
  import { appAppearance } from '../../app/app.constants'

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  app_instance:AppInstance
  estadoConexion:string
  
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public toast :  ToastController,
    private network : Network,
    public eventProvider: EventProvider,
    private database: DatabaseProvider) {

    this.app_instance = appAppearance

    this.network.onConnect().subscribe(()=>{
      this.estadoConexion = "Conectado a internet";
      console.log("heyyy");
      this.toast.create({
        message: 'Conectado a internet',
        duration: 3000
      }).present();
    });

    this.network.onDisconnect().subscribe(()=>{
      this.toast.create({
        message: 'Desconectado a internet',
        duration: 3000
      }).present();
    });
    
  }

  goToLoginNative(){

    this.navCtrl.push(LoginNativePage)

  }

  goToRegisterPage(){
    
    this.navCtrl.push(RegisterPage)

  }

  goToForgotPage(){
    
    this.navCtrl.push(ForgotPage)

  }

  ionViewDidLoad() {

    console.log('ionViewDidLoad LoginPage')

  }

}



